export default {
	data() {
		return {
			details: {},
			uid: "",
			pid: "",
			//控制渐变标题栏的参数
			beforeHeaderzIndex: 11, //层级
			afterHeaderzIndex: 10, //层级
			beforeHeaderOpacity: 1, //不透明度
			afterHeaderOpacity: 0, //不透明度
			//是否显示返回按钮



			//轮播主图数据
			swiperList: [],
			//轮播图下标
			currentSwiper: 0,
			anchorlist: [], //导航条锚点
			selectAnchor: 0, //选中锚点
			serviceClass: '', //服务弹窗css类，控制开关动画
			specClass: '', //规格弹窗css类，控制开关动画
			shareClass: '', //分享弹窗css类，控制开关动画
			// 商品信息
			goodsData: {
				id: 1,
				name: "商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题",
				price: "127.00",
				number: 1,
				service: [],
				spec: ["当面交易", "蓝沢担保", "线上发货"],
				comment: {}
			},
			selectSpec: null, //选中交易模式
			isKeep: false, //收藏
			//商品描述html
			descriptionStr: ""
		};
	},
	onLoad(options) {
		this.pid = options.pid;
		console.log(this.pid, "(((((((((((((((((((())))))))))))))))))))")

		//小程序隐藏返回按钮
		this.showBack = false;

		//option为object类型，会序列化上个页面传递的参数
		console.log(options.pid); //打印出上个页面传递的参数。
	},
	onReady() {
		this.calcAnchor(); //计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
		this.todetails() //请求商品的详细信息；
	},
	onPageScroll(e) {
		//锚点切换
		this.selectAnchor = e.scrollTop >= this.anchorlist[2].top ? 2 : e.scrollTop >= this.anchorlist[1].top ? 1 : 0;
		//导航栏渐变
		let tmpY = 375;
		e.scrollTop = e.scrollTop > tmpY ? 375 : e.scrollTop;
		this.afterHeaderOpacity = e.scrollTop * (1 / tmpY);
		this.beforeHeaderOpacity = 1 - this.afterHeaderOpacity;
		//切换层级
		this.beforeHeaderzIndex = e.scrollTop > 0 ? 10 : 11;
		this.afterHeaderzIndex = e.scrollTop > 0 ? 11 : 10;
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		// uni.showToast({
		// 	title: '触发上拉加载'
		// });
	},
	mounted() {

	},
	methods: {
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '../msg/msg'
			})
		},
		// 客服
		toChat() {
			var status=true;
			var uid = this.uid;
			var summsg = {};
			uni.getStorage({
				key: "summsg",
				success: res => {
					summsg = res.data;
					console.log(res.data, "聊天的数据")
				},
				fail: res => {
					console.log("失败");
					summsg[uid] = [
						[]
					];
					// console.log("@@@@@@@@@@@@@@@@@@@@@@@@@", item[0], item[1], item[2], item);
					summsg[uid][0] = [];
					// console.log("&&&&&&&&&&&&&&&&&&&&&", summessage[this.uid][0]);
					summsg[uid][1] = [];
					summsg[uid][2] = '';
					summsg[uid][0].push("2018-01-01");
					summsg[uid][1].push({
						"user": "me",
						"msg": "hello"
					});
					summsg[uid][2] = uid;
					uni.setStorage({
						key: "summsg",
						data: summsg,
						success: res => {
							console.log("成功")
							status=false;
						}
					})
				}
			})
			
			if (status) {
				if (summsg[uid] == undefined) {
					summsg[uid] = [
						[]
					];
					// console.log("@@@@@@@@@@@@@@@@@@@@@@@@@", item[0], item[1], item[2], item);
					summsg[uid][0] = [];
					// console.log("&&&&&&&&&&&&&&&&&&&&&", summessage[this.uid][0]);
					summsg[uid][1] = [];
					summsg[uid][2] = '';
					summsg[uid][0].push("2019-01-01");
					summsg[uid][1].push({
						"user": "me",
						"msg": "hello"
					});
					summsg[uid][2] = uid;
				} else {
					summsg[uid][0].push("2019-01-01");
					summsg[uid][1].push({
						"user": "me",
						"msg": "hello"
					});
					summsg[uid][2] = uid;
					console.log("发送一次")
				}
				uni.setStorage({
					key: "summsg",
					data: summsg,
					success: res => {
						var data = {};
						uni.getStorage({
							key: "token",
							success: res => {
								data.token = res.data;
							},
							fail: res => {
								uni.showToast({
									title: "请您登陆之后在进行此操作",
									icon: "none"
								})
							}
						});
						data.ruid = uid;
						data.type = "add";
						data.message = "hello!";
						if (data.ruid && data.token) {
							uni.request({
								url: 'http://120.79.19.253:10086/Chat',
								data,
								success: (res) => {
									console.log(res, "商品详情发起对话");
									uni.navigateTo({
										url:'../msg/msg'
									})
								}
								
							});
							console.log("消息发送成功过");
							console.log("发消息函数被触发")
						}
				
				
						// uni.navigateTo({
						// 	url: "../msg/chat/chat?name=客服008"
						// })
					},
					fail: res => {
						uni.showToast({
							title: "联系客服失败，稍后再试",
							icon: "none"
						})
					}
				})
			}

		},
		todetails() {
			console.log("请求商品的详细数据");
			uni.request({
				url: "http://120.79.19.253:10086/Search",
				data: {
					type: "goods",
					pid: this.pid
				},
				success: (res) => {
					// console.log(res.data);
					if (res.data.status == 1) {
						var data = res.data;
						this.text = 'request success';
						var {
							p_description,
							pid,
							pimages,
							pname,
							status,
							uid,
							price,
							wcount
						} = data;
						this.uid = uid;
						this.goodsData.name = pname;
						this.goodsData.pid = pid;
						this.goodsData.price = price;
						if (status == 1) {
							this.goodsData.service = [{
									name: "蓝沢担保",
									description: "此商品官方保证为正品"
								},
								{
									name: "极速退款",
									description: "此商品享受退货极速退款服务"
								},
								{
									name: "7天退换",
									description: "此商品享受7天无理由退换服务"
								}
							]
						}
						this.todetails = data;
						var swiperList = [];
						var reg1 = / ;/;
						var reg2 = /;/;
						if (reg1.test(pimages)) {
							pimages = pimages.split(" ;");
							for (var item in pimages) {
								swiperList.push({
									id: item + 1,
									img: pimages[item]
								})
							}
						}
						else if (reg2.test(pimages)) {
							pimages = pimages.split(";");
							for (var item in pimages) {
								swiperList.push({
									id: item + 1,
									img: pimages[item]
								})
							}
						}else {
							swiperList.push({
								id: 1,
								img: pimages
							})
						}
						this.swiperList = swiperList;
						if (this.swiperList.length === 1) {
							this.descriptionStr = `<div style="text-align:center; margin-bottom: 50px;"><img width="100%" src="${this.swiperList[0].img}"/></div>`
						} else {
							this.descriptionStr = `<div style="text-align:center; margin-bottom: 50px;">`;
							for (var item of this.swiperList) {
								this.descriptionStr += `<img width="100%" src="${item.img}"/>`;
							}
							this.descriptionStr += `</div>`;
						}
						// console.log(this.descriptionStr, "dfgfdsfghjhgfdsfghjkhjgfdsghjkhgfdfghjkjgfd");
					} else {
						uni.showToast({
							title: "商品不存在",
							// icon:"loading",
							position: "center",
							image: "../../static/img/myfail.png"

						});
						this.swiperList = [{
								id: 1,
								img: 'https://ae01.alicdn.com/kf/HTB1Mj7iTmzqK1RjSZFjq6zlCFXaP.jpg'
							},
							{
								id: 2,
								img: 'https://ae01.alicdn.com/kf/HTB1fbseTmzqK1RjSZFLq6An2XXaL.jpg'
							},
							{
								id: 3,
								img: 'https://ae01.alicdn.com/kf/HTB1dPUMThnaK1RjSZFtq6zC2VXa0.jpg'
							},
							{
								id: 4,
								img: 'https://ae01.alicdn.com/kf/HTB1OHZrTXzqK1RjSZFvq6AB7VXaw.jpg'
							}
						]
					}
				}
			});
			uni.request({
				url: "http://120.79.19.253:10086/Leftmessage",
				data: {
					type: "get",
					pid: this.pid
				},
				success: (res) => {
					console.log(res.data.status, "fghjklkhgfhjhgfhjkhgfjkhgggggggggggggggggggggg");
					if (res.data.status == 0) {
						this.goodsData.comment = {
							number: 0,
							userface: "https://simplett-img.oss-cn-beijing.aliyuncs.com/user_image/userimage.jpg",
							username: "系统提示",
							content: "此商品的人气还不够哦"
						}
					} else {
						var mydata = res.data.data;
						if (mydata) {
							this.goodsData.comment = {
								number: mydata.length,
								userface: mydata[0].image,
								username: mydata[0].nickname,
								content: mydata[0].message
							}
						}

					}
				}
			})
		},
		// 分享
		share() {
			this.shareClass = 'show';
		},
		hideShare() {
			this.shareClass = 'hide';
			setTimeout(() => {
				this.shareClass = 'none';
			}, 150);
		},
		//收藏
		keep() {
			this.isKeep = this.isKeep ? false : true;
		},
		// 加入购物车
		joinCart() {
			var token;
			uni.getStorage({
				key:"token",
				success: res => {
					token=res.data;
				}
			});
			if(token)
			{
			var id = this.pid;
			var img = this.swiperList[0].img;
			var price = this.goodsData.price;
			var name = this.goodsData.name;
			var number = 1;

			var sclist = {
				id,
				img,
				price,
				name,
				number,
				spec: "",
				selected: false
			}
			var shoucanlists = [];
			var status = "";
			uni.getStorage({
				key: "shoucanlist",
				success: res => {
					// console.log(res.data);
					status = res.data;
				},
				fail: res => {
					status = false;
				}
			});
			console.log(status, "tttttttttttttttttttttttttttttttttt");
			if (this.selectSpec !== null) { //判断商品是否选择规格
				if (status == false) { //商品列表里面没有数据
					sclist.spec = this.selectSpec;
					shoucanlists.push(sclist);
					console.log("ppppppppppppppppppp", shoucanlists)
					uni.setStorage({
						key: "shoucanlist",
						data: shoucanlists,
						success: (res) => {
							uni.showToast({
								title: "已加入购物车"
							});
						},
						fail() {
							uni.showToast({
								title: "储存异常，加入购物车失败"
							});
						}
					})
				} else { //商品列表里面有数据
					shoucanlists = status;
					var iSshoucan = false;
					for (var item of shoucanlists) {
						if (item.id == id) {
							iSshoucan = true;
						}
					}
					console.log(iSshoucan, "ASAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAA", id);
					if (!iSshoucan) //判断收藏列表里面是否有该商品
					{
						sclist.spec = this.selectSpec;
						shoucanlists.push(sclist);
						uni.setStorage({
							key: "shoucanlist",
							data: shoucanlists,
							success: res => {
								uni.showToast({
									title: "已加入购物车"
								});
							},
							fail: res => {
								uni.showToast({
									title: "储存异常，加入购物车失败"
								});
							}
						})
					} else {
						uni.showToast({
							title: "已存在购物车"
						});
					}

				}

			} else {
				uni.showToast({
					title: "请选择交易模式之后再加入购物车",
					icon: "none"
				});
			}
			}else
			{
				uni.showToast({
					title:"您未登陆或着登陆状态异常",
					icon:"none"
				})
			}

		},
		//收藏
		addlike() {
			var token = "";
			uni.getStorage({
				key: "token",
				success: res => {
					token = res.data;
					var data = {
						token,
						pid:this.pid,
						type: "production",
						action: "add"
					}
					uni.request({
						url: 'http://120.79.19.253:10086/Subscribe',
						data,
						success: res => {
							console.log(res.data, "hhhhhhhhhh");
							if (res.data.status == 1) {
								uni.showToast({
									title:"收藏成功"
								});
								this.keep()
							} else {
								uni.showToast({
									title:"失败",
									icon:"none"
								})
							}
							// this.text = 'request success';
						}
					});
				},
				fail: () => {
					uni.showToast({
					title: "请登陆之后再使用此功能",
					icon:"none"
				})
				}
			})	
		},
		//立即购买
		buy() {
			var token;
			uni.getStorage({
				key:"token",
				success: res => {
					token=res.data;
				}
			});
			if(token)
			{
			if (this.selectSpec == null) {
				return this.showSpec(() => {
					this.toConfirmation();
				});
			}
			this.toConfirmation();
			}else{
				uni.showToast({
					title:"您未登陆或着登陆状态异常",
					icon:"none"
				})
			}
		},
		//商品评论
		toRatings() {
			uni.navigateTo({
				url: 'ratings/ratings?pid=' + this.pid
			})
		},
		//跳转确认订单页面
		toConfirmation() {
			let tmpList = [];
			let goods = {
				id: this.goodsData.id,
				img: this.swiperList[0].img,
				name: this.goodsData.name,
				spec: '交易模式:' + this.goodsData.spec[this.selectSpec],
				price: this.goodsData.price,
				number: this.goodsData.number
			};
			tmpList.push(goods);
			uni.setStorage({
				key: 'buylist',
				data: tmpList,
				success: () => {
					uni.navigateTo({
						url: '../order/confirmation'
					})
				}
			})
		},
		//跳转评论列表
		showComments(goodsid) {

		},
		//选择交易模式
		setSelectSpec(index) {
			this.selectSpec = index;
		},
		//减少数量
		sub() {
			if (this.goodsData.number <= 1) {
				return;
			}
			// this.goodsData.number--;
			this.goodsData.number = 1;
		},
		//增加数量
		add() {
			// this.goodsData.number++;
			this.goodsData.number = 1;
		},
		//跳转锚点
		toAnchor(index) {
			this.selectAnchor = index;
			uni.pageScrollTo({
				scrollTop: this.anchorlist[index].top,
				duration: 200
			});
		},
		//计算锚点高度
		calcAnchor() {
			this.anchorlist = [{
					name: '主图',
					top: 0
				},
				{
					name: '评价',
					top: 0
				},
				{
					name: '详情',
					top: 0
				}
			]
			let commentsView = uni.createSelectorQuery().select("#comments");
			commentsView.boundingClientRect((data) => {
				let statusbarHeight = 0;
				//APP内还要计算状态栏高度



				let headerHeight = uni.upx2px(100);
				this.anchorlist[1].top = data.top - headerHeight - statusbarHeight;
				this.anchorlist[2].top = data.bottom - headerHeight - statusbarHeight;

			}).exec();
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		//服务弹窗
		showService() {
			console.log('show');
			this.serviceClass = 'show';
		},
		//关闭服务弹窗
		hideService() {
			this.serviceClass = 'hide';
			setTimeout(() => {
				this.serviceClass = 'none';
			}, 200);
		},
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback() {
			return;
		},
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调

			this.selectSpec && this.specCallback && this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		discard() {
			//丢弃
		}
	}
};
